import Router from 'vue-router'
import Login from '@/components/Login'
import TabPage from '@/components/TabPage'
import Container from '@/components/Container'
import AnnoPage from '@/components/AnnoPage'
import BrowseData from '@/components/BrowseData'
import QueryType from '@/components/QueryType'
import UnRecognizePage from '@/components/UnRecognizePage'
import AnnoQueryPage from '@/components/AnnoQueryPage'
export default new Router({
  mode:'history',
  routes: [
    {
      path:'/',
      name:'标注模块',
      component:Container,
      children:[
        {path:'/annoPage',component:AnnoPage,name:'标注'},
        {path:'/unRecognizePage',component:UnRecognizePage,name:'待定'},
        {path:'/annoQueryPage',component:AnnoQueryPage,name:'标注查询'}
      ]
    },
    {
      path:'/',
      name:'类型模块',
      component:Container,
      children:[
        {path:'/browseData',component:BrowseData,name:'浏览数据'},
        {path:'/queryType',component:QueryType,name:'查询类型'}
      ]
    },
    {
      path:'/login',
      name:'Login',
      component:Login,
      hidden:true
    },
    {
      path: '/',
      name: '',
      component: Container,
      leaf:true,
      children:[
        {path:'/tabPage',component:TabPage,name:'标签页'}
      ]
    },
    {
      path:'*',
      hidden:true,
      redirect:{path:'/login'}
    }
  ]
})



